import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import http from '@/components/utils/http.js';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navItem:[],
			serverapi:this.apiServer,
			statepage:[0,0,0,0],
			page:1,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '完成',
					loadingType: 'more',
					orderList: []
				}
				
			],
		};
	},
	
	onLoad(options){
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = +options.state;




		if(options.state == 0){
			this.loadData()
		}

		
	},
	 
	methods: {
		//获取订单列表
		loadData(source){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			if(source === 'tabChange' && navItem.loaded === true){
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType === 'noMore'){
				//已经加载完成，无需要重新加载
				return;
			}
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';
			//this.navItem=navItem;
			let page=this.statepage[index]+1;
			this.statepage[index]=page;
			
			this.getorderdata(index,navItem,page);
				
		}, 
		getorderdata(state,navItem,page){
			
			let opts = {
				url: '/orderapi/getorderlist/',
				method: 'post'
			};
			
			let param = {state:state,page:page};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						let serverdata=res.data['list'] ;
						console.log(serverdata);
						setTimeout(()=>{
							let orderList = serverdata.filter(item=>{
								//添加不同状态下订单的表现形式
								item = Object.assign(item, this.orderStateExp(item.state));
								//演示数据所以自己进行状态筛选
								if(state === 0){
									//0为全部订单
									return item;
								}
								return item.state === state
							});
							orderList.forEach(item=>{
								navItem.orderList.push(item);
							})
							console.log("serverdata.length=",serverdata.length);
							this.$set(navItem, 'loaded', true);
							//判断是否还有数据， 有改为 more， 没有改为noMore 
							if(serverdata.length==0){
								navItem.loadingType = 'noMore';
								
							}else{
								//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
								navItem.loadingType = 'more';
							}
						}, 600);
						
					} else {
						uni.showToast({ title: res.data.msg, icon: 'none' });
					}
				},
				error => {
					console.log(error);
				}
			);
			
			
		},

		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(id,index){
			let _that=this;
			uni.showLoading({
				title: '请稍后'
			})
			
			
			let opts = {
				url: '/orderApi/deleteOrder/',
				method: 'post'
			};
			let param = {id:id};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						setTimeout(()=>{
							_that.navList[_that.tabCurrentIndex].orderList.splice(index, 1);
							uni.hideLoading();
						}, 600)
						
					} else {
						uni.showToast({ title: res.data.msg, icon: 'none' });
					}
				},
				error => {
					console.log(error);
				}
			);
			
			
			
		},
		//取消订单
		cancelOrder(item){
			
			let _that=this;
			uni.showLoading({
				title: '请稍后'
			})
			let opts = {
				url: '/orderApi/cancelorder/',
				method: 'post'
			};
			
			
			let param = {id:item.id};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						setTimeout(()=>{
							let {stateTip, stateTipColor} = _that.orderStateExp(9);
							item = Object.assign(item, {
								state: 9,
								stateTip, 
								stateTipColor
							})
							
							//取消订单后删除待付款中该项
							let list = _that.navList[1].orderList;
							let index = list.findIndex(val=>val.id === item.id);
							index !== -1 && list.splice(index, 1);
							uni.hideLoading();
						}, 600)
						
					} else {
						uni.showToast({ title: res.data.msg, icon: 'none' });
					}
				},
				error => {
					console.log(error);
				}
			);
			
			
			
		},

		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		},
		
		payOrder(item){
			uni.navigateTo({
				url: '../money/pay?oid='+item.id
			});
		},
		
		disporder(item){
			uni.navigateTo({
				url: 'dispOrder?id='+item.id
			});
		}
		
		
	},
}
